import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { Col, Row } from 'reactstrap';
import { Checkbox, EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, CodigoDctf } from '../../../../models/dtos/mlf/codigoDctf';

interface CodigoDctfEditViewProps extends EditViewProps<CodigoDctf> {}

const CodigoDctfEditView: FC<CodigoDctfEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<CodigoDctf>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Códigos DCTF para PIS/COFINS" wikiId={7521}>
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true, maxLength: 6 } }}
              id="codigo"
              isPrependLabel={true}
              label="Código"
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline formProps={formProps} id="descricao" isPrependLabel={true} label="Descrição" />
          </Col>
        </Row>
        <Row>
          <Col>
            <Checkbox id="pis" label="PIS" formProps={formProps} />
          </Col>
        </Row>
        <Row>
          <Col style={{ marginTop: 10 }}>
            <Checkbox id="cofins" label="COFINS" formProps={formProps} />
          </Col>
        </Row>
      </EditForm>
    </WindowComponent>
  );
};

export default CodigoDctfEditView;
